dl.outro {
    float: left;
    margin: 0px auto;
    width: 850px;
    text-align: left;
    // border: 1px solid #e4e8f5;
    // box-shadow: 0px 4px 0px #fefeff;
    // -moz-box-shadow: 0px 4px 0px #fefeff;
    // -webkit-box-shadow: 0px 4px 0px #fefeff;
    // border-bottom-color: #d9def1;
    // -khtml-border-radius: 5px;
    // -moz-border-radius: 5px;
    // -webkit-border-radius: 5px;
    // border-radius: 5px;
    box-sizing: border-box;
    position: relative;
    padding: 0 0 20px 0;
}

dl.outro dd {
    font-size: 0.75em;
    line-height: 1.9em;
    color: #a5b3be;
    display: block;
    margin-inline-start: 0px;
}

dl.outro .left {
    float: left;
    width: 23.8%;
    padding: 14px 8% 35px 0%;
}

dl.outro .center {
    float: left;
    width: 33.8%;
    padding: 14px 0 35px;
}

dl.outro .right {
    float: right;
    width: 23.8%;
    padding: 14px 0% 35px 8%;
}


.footer-container{
    width: 100%;
    display: flex;

    text-align: center;
    // margin: 30px 5%;
    // min-height: 100px;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);

}

@media only screen and (max-width: 450px){
    body .container dl.outro .col {
        padding: 0 15%;
        width: 70%;
    }
}