html, body {
  margin: 0;
  padding: 0;
  font-family: "Work Sans", sans-serif;
  font-size: 100%;
  line-height: 1.6em;
  color: #505a6a; }

h1, h2, h3, h4, h5, p, li {
  margin: 0 0;
  color: black;
  font-family: Work Sans, sans-serif;
  width: fit-content; }

h1 {
  font-size: 3rem;
  line-height: 3.5rem;
  font-weight: 400;
  margin-bottom: 0.5rem; }

h2 {
  font-size: 2rem;
  line-height: 3.25rem;
  font-weight: 400;
  margin-bottom: 0.5rem; }

h3 {
  font-size: 1.5rem;
  line-height: 3rem;
  font-weight: 300;
  margin-bottom: 0.5rem; }

h4 {
  font-size: 1.25rem;
  line-height: 2.25rem;
  font-weight: 700;
  margin-bottom: 0.5rem; }

h5 {
  font-size: 1.25rem;
  line-height: 2.25rem;
  font-weight: 400;
  margin-bottom: 0.5rem; }

p {
  font-size: 1rem;
  line-height: 2rem;
  font-weight: 400;
  margin-bottom: 0.5rem; }

code {
  font-size: 1.5rem;
  line-height: 1.75rem;
  font-weight: 300;
  display: inline-block;
  padding: 0.25rem;
  border: 1px solid #d1d5da;
  border-radius: 0.25rem;
  color: #f97583; }

li {
  font-size: 1.5rem;
  line-height: 1.75rem;
  font-weight: 300;
  list-style-type: none; }

ul {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px; }

a {
  color: inherit; }

/* ––––––––––––––––––––––––––––––––––––– */
/* ––––––––––––––––––––––––––––––––––––– */
.header-type-01 {
  margin: 2rem 0 1rem;
  font-weight: 500; }

.type-link {
  font-weight: 500;
  color: #d1d5da; }

/* ––––––––––––––––––––––––––––––––––––– */
/* ––––––––––––––––––––––––––––––––––––– */
/* Design Section Pages */
/* Font Size */
ul.font-size > li {
  padding: 0.5rem 0.5rem; }

/* Font Weights */
ul.font-weights > li {
  padding: 0.5rem 0.5rem; }

.font-weights li:first-child {
  font-weight: 300; }

.font-weights li:nth-child(2) {
  font-weight: 400; }

.font-weights li:nth-child(3) {
  font-weight: 500; }

.font-weights li:last-child {
  font-weight: 700; }

/* Layout Scale */
ul.layout-scale > li {
  background-color: #b392f0;
  margin: 0.5rem 0.5rem; }

.layout-scale li:first-child {
  height: 1rem;
  width: 1rem; }

.layout-scale li:nth-child(2) {
  height: 1.5rem;
  width: 1.5rem; }

.layout-scale li:nth-child(3) {
  height: 2rem;
  width: 2rem; }

.layout-scale li:nth-child(4) {
  height: 3rem;
  width: 3rem; }

.layout-scale li:nth-child(5) {
  height: 4rem;
  width: 4rem; }

.layout-scale li:nth-child(6) {
  height: 6rem;
  width: 6rem; }

.layout-scale li:nth-child(7) {
  height: 10rem;
  width: 10rem; }

/* Spacing Scale */
ul.spacing-scale > li {
  background-color: #b392f0;
  margin: 0.5rem 0.5rem; }

.spacing-scale li:first-child {
  width: 0.125rem;
  height: 0.125rem; }

.spacing-scale li:nth-child(2) {
  width: 0.25rem;
  height: 0.25rem; }

.spacing-scale li:nth-child(3) {
  width: 0.5rem;
  height: 0.5rem; }

.spacing-scale li:nth-child(4) {
  width: 0.75rem;
  height: 0.75rem; }

.spacing-scale li:nth-child(5) {
  width: 1rem;
  height: 1rem; }

.spacing-scale li:nth-child(6) {
  width: 1.5rem;
  height: 1.5rem; }

.spacing-scale li:nth-child(7) {
  width: 2rem;
  height: 2rem; }

/* Buttons Hover and more */
ul.button-hovers-more > li {
  background-color: null; }

/* Internal Link Styles */
.internal-link {
  transition: all 0.2s ease-in-out;
  padding-bottom: -1em;
  box-shadow: inset 0 -1px 0 rgba(27, 31, 35, 0.15); }

.internal-link:hover {
  transition: all 0.6s ease-in-out;
  text-decoration: none;
  box-shadow: inset 0 -18px 0 rgba(27, 31, 35, 0.15); }

/* External Link Styles */
.external-link {
  transition: all 0.2s ease-in-out;
  color: #6a737d; }

.external-link:hover {
  transition: all .15s ease-in;
  color: #000; }

a:-webkit-any-link {
  color: none;
  cursor: pointer;
  text-decoration: none; }

.container {
  margin: 0px auto;
  width: 850px;
  position: relative;
  display: flex;
  flex-direction: column; }

#app-container {
  height: 100%;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column; }

.large-margin-bottom {
  margin-bottom: 4em; }

#profile-image {
  width: 10rem;
  height: 10rem;
  margin-bottom: 1rem; }

@media only screen and (max-width: 680px) {
  ul.caseStudies li {
    width: 100%;
    margin: 0 0 50px 0; } }

@media only screen and (max-width: 450px) {
  body .container {
    width: 96%;
    margin: 0 2%; }
  #about {
    width: 100%; } }

@media only screen and (max-width: 980px) {
  .container {
    float: left;
    width: 90%;
    margin: 0 5%; } }

.content {
  margin-top: 6rem; }

.layout-grid-rows {
  display: flex;
  flex-basis: auto;
  flex-direction: column; }

.layout-grid-columns {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-flow: row wrap; }

#header {
  float: left;
  width: 100%;
  margin-top: 0em;
  margin-bottom: 4rem;
  max-width: 800px; }

.page {
  box-sizing: border-box;
  margin-top: 10rem;
  margin-bottom: 10rem; }

.timeline {
  padding-left: 1.5rem;
  border-left: 1px solid rgba(0, 0, 0, 0.3); }
  .timeline li {
    margin-bottom: 3rem; }

.horizontal-card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  min-height: 8em;
  max-width: 40em;
  border: 1px solid #d1d5da;
  border-radius: 0.5rem;
  margin-bottom: 1.5rem;
  transition: all 0.2s ease-in-out;
  overflow: hidden; }
  .horizontal-card .card-left {
    background-color: #d1d5da;
    flex: 4;
    align-self: stretch; }
  .horizontal-card .card-right {
    padding: 2rem;
    flex: 6;
    align-self: stretch;
    justify-content: center;
    display: flex;
    flex-direction: column; }

.horizontal-card:hover {
  transition: all 0.2s ease-in-out;
  background-color: #e1e4e8; }

.horizontal-card:hover .external-link {
  transition: all .15s ease-in;
  color: #000; }

.vertical-card {
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-basis: 45%;
  max-width: 30em;
  border: 1px solid #d1d5da;
  border-radius: 0.5rem;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  transition: all 0.2s ease-in-out;
  overflow: hidden; }
  .vertical-card .card-top {
    width: 100%;
    height: 30em;
    background-color: #d1d5da;
    align-self: stretch; }
  .vertical-card .card-bottom {
    padding: 2rem;
    align-self: stretch;
    justify-content: center;
    display: flex;
    flex-direction: column; }

.vertical-card:hover {
  transition: all 0.2s ease-in-out;
  background-color: #e1e4e8; }

.vertical-card:hover .external-link {
  transition: all .15s ease-in;
  color: #000; }
